import React from "react"
import { graphql } from 'gatsby';
import { Link } from "gatsby"
import Layout from "../components/layout"
//import Image from "../components/image"
import SEO from "../components/seo"

import { useSiteMetadata } from '../hooks/useSiteMetadata';


export default ({ data }) => {
  const { description } = useSiteMetadata();
  return (
    <>
      <Layout>
        <SEO title={description}/> 
        {data.allMdx.nodes.map(({ frontmatter, fields }) => (
          <div key={fields.slug}>
            <Link to={fields.slug} style={{textDecoration: 'none'}}>
              <h2 style={{color: '#217346'}}>{frontmatter.title}</h2>              
            </Link>
            <p className='mb-5'>{frontmatter.date} {frontmatter?.excerpt ? frontmatter?.excerpt : ' '}</p>
          </div>
        ))}
      </Layout>
    </>
  );
};


export const query = graphql`
  query SITE_INDEX_QUERY {
    allMdx(
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { published: { eq: true } } }
    ) {
      nodes {
        id
        excerpt(pruneLength: 250)
        frontmatter {
          title
          date
          excerpt
        }
        fields {
          slug
        }
      }
    }
  }
`;


